import { punchinLimitTime } from "./config";

// 补0
export function add0(val) {
  return val >= 10 ? val + '' : '0' + val
}

// 获取HTML的fontSize（1rem）
export function htmlFontSize() {
  const htmlDom=document.getElementsByTagName('html')[0];
  return parseFloat(htmlDom.style.fontSize)
}

// 日期格式转换
export function parseTime(time, cFormat) {
  // debugger
  if (arguments.length === 0) {
    return null
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
  let date
  if (typeof time === 'object') {
    date = time
  } else {
    if (('' + time).length === 10) time = parseInt(time) * 1000
    date = new Date(+time)
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }
  const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key]
    if (key === 'a') { return ['一', '二', '三', '四', '五', '六', '日'][value - 1] }
    if (result.length > 0 && value < 10) {
      value = '0' + value
    }
    return value || 0
  })
  return time_str
}


// 手机号码校验
export function isPhoneNum(tel) {
  const reg = /^1[0-9]{10}$/
  tel += ''
  return !!tel.match(reg)
}

export function isToday(date) {
  return new Date().toDateString() === date.toDateString();
}

// 获取url 参数 （根据search的key获取相应value）
export function getUrlParam(name) {
  const reg  = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
  const result  = window.location.search.substr(1).match(reg);
  return result ? decodeURIComponent(result[2]) : null;
}

// 是否在规定时间内
export function withinLimitTime() {
  const nowTime = +new Date()
  const startTime = +(new Date()).setHours(punchinLimitTime[0][0], punchinLimitTime[0][1], 0, 0)
  const endtTime = +(new Date()).setHours(punchinLimitTime[1][0], punchinLimitTime[1][1], 0, 0)
  if(nowTime<=startTime || nowTime>=endtTime) {
    return false
  } else {
    return true
  }
}

export function getClientHeight() {
  return window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
}


export function getBase64Image(img) {
  console.log('export function getBase64Image');
  
  var image = new Image();
  image.src = img;
  image.setAttribute("crossOrigin", 'Anonymous')//解决跨域问题
  return new Promise((resolve, reject) => {
    image.onload = function () {
      console.log('image.onload', image);
      var canvas = document.createElement("canvas");
      canvas.width = image.width;
      canvas.height = image.height;
      console.log(image.height);
      
      var ctx = canvas.getContext("2d");
      ctx.drawImage(image, 0, 0, image.width, image.height);
      console.log(ctx);
      try {
        var dataURL = canvas.toDataURL("image/png");
      } catch(e) {
        console.log('canvas.toDataURL fail');     
      }
      console.log(dataURL);
      
      resolve(dataURL)
    }
    image.onerror = function () {
      console.log('image.onerror');
      reject('图片加载失败')
    }
  })

}

// 节流
export function throttleFn(fn, delay = 1000) {
  let timer = null;
  return function (...rest) {
    if (timer) return
    timer = setTimeout(() => {
      timer = null;
    }, delay);
    fn.apply(this, rest);
  }
}
// 防抖 当wait毫秒内没有触发事件时，才会延时触发fn
export function debounceFn(fn, wait = 500, IIFEFn) {
  let timeout = null;
  return function (...rest) {
    !timeout && IIFEFn && IIFEFn()
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      fn.apply(this, rest);
      timeout = null;
    }, wait);
  }
}