import Vue from 'vue'
import Vuex from 'vuex'
import {ifremeber, wxInit} from "../api/clog";
import {getUrlParam, parseTime} from "../utils/tools";

Vue.use(Vuex)

const state = {
  userInfo: {
    openid: getUrlParam('openid'),
    // openid: 'olcf0sxfPuzYO6ttRZfFumgoMkw8',
    // openid: 'olcf0szr9Ai8qmi88CJJtJGWCKdE',
    nickname: null,
    headImg: require('../views/clock/imgs/head-img.png'),
  },
  pinchinInfo: {
    punchinTotal: '',            //累计打卡次数,
    continueNum: '',             //连接打卡次数,
    lastPunchinTime: '',         //最后一次打卡时间,
    remindFlag: '',              //是否开启自定义提示0不开启、1开启,
    remindType: '',              //提醒类型0系统智能提醒、1自定义提醒,
    remindTime: '',              //提醒时间HH:mm
    hasRequest: false,           // 自己设置的变量（是否请求过打卡情况）
    replyTime: '',
    signCardNum: null,              // 补签卡数量
  }
}
const getters = {
  hasPunchin: state => {
    const lastPunchinTime = state.pinchinInfo.lastPunchinTime || '1990-01-02 00:00:00'
    const lastPday = new Date(lastPunchinTime.split(' ')[0])
    const today = new Date(state.pinchinInfo.replyTime)
    let todayStr = parseTime(today, '{yyyy}-{mm}-{dd}')
    let lastPdayStr = parseTime(lastPday, '{yyyy}-{mm}-{dd}')
    return todayStr === lastPdayStr
  }
}
const mutations = {
  setUserInfo(state, newUserInfo) {
    Object.assign(state.userInfo, newUserInfo)
  },
  setPinchinInfo(state, newPinchinInfo) {
    Object.assign(state.pinchinInfo, newPinchinInfo)
    if (state.pinchinInfo.signCardNum < 0 || state.pinchinInfo.signCardNum == undefined) {
      state.pinchinInfo.signCardNum = 0
    }
  }
}
const actions = {
  getWxconfig({dispatch, state}) {
    return wxInit().then(res => {
      wx.config({
        // debug: process.env.NODE_ENV === 'development' ? true : false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        debug:  false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        appId: res.appId, // 必填，公众号的唯一标识
        timestamp: res.timestamp, // 必填，生成签名的时间戳
        nonceStr: res.nonceStr, // 必填，生成签名的随机串
        signature: res.signature, // 必填，签名
        jsApiList: [
          'onMenuShareTimeline',
          'onMenuShareAppMessage',
          'hideMenuItems',
          'closeWindow',
          'showMenuItems',
          'scanQRCode',
          'chooseImage',
          'previewImage',
          'getLocation',
          'uploadImage',
          'downloadImage',
          'startRecord',
          'playVoice',
          'stopRecord',
          'translateVoice',
          'onVoiceRecordEnd',
          'uploadVoice',
          'hideAllNonBaseMenuItem'
        ] // 必填，需要使用的JS接口列表
      });
      wx.ready(function () {
        try {
          wx.hideOptionMenu();
          wx.hideAllNonBaseMenuItem();
        } catch(e) {
          console.log('wx.hideMenuItems', e)
        }
      })
      // dispatch('getUserWxInfo')
    }).catch (e => {
      // location.search = ''
    })
  },
  getUserWxInfo({commit, state}) {
    return ifremeber(state.userInfo.openid).then(res => {
      const {nickname, headimgurl:headImg} = res
      commit('setUserInfo',{nickname, headImg})
    })
  }
}
export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})
