import request, {baseUrl} from '../utils/request';
//查看是否关注，返回昵称头像等
export function ifremeber(openid){
    return request({
      baseURL: baseUrl.vURL,
      url: `/wx3/uinfo2?openid=${openid}&appid=${baseUrl.appid}`,
      method: 'get'
    })
};

// wx jssdk config
export function wxInit(){
    return request({
      baseURL: baseUrl.vURL,
      url: `/wx3/vxsign?appid=${baseUrl.appid}`,
      method: 'get'
    })
};
