import router from './router'
import store from './store'
import {getUrlParam} from "./utils/tools";
let redirect = ''

router.beforeEach((to, from, next) => {
  // console.log(to,from.path)
  if(redirect === '') {
    redirect = getUrlParam('router') || 'home'
    if (store.state.userInfo.nickname === null) {
      store.dispatch('getWxconfig').then(res => {})
      store.dispatch('getUserWxInfo').then(res => {})
    }
    if(sessionStorage.getItem('router') !== null) {
      next()
    } else {
      next({name: redirect, replace: true})
    }
    sessionStorage.setItem('router', redirect)

  } else {
    next()
  }
})

router.afterEach((to, from) => {
  wx.hideOptionMenu();
  document.title = to.meta.title
})
